// This module is designed to provide access to the HTML elements of components that use the react-jsx-template 


// returns a non-live array of nodes
module.exports.getElementsByClassName = function getElementsByClassName (className) {
    let host = this.state.component,
    componentShadowRoot = host.shadowRoot,
    elements = null

    if (className !== '') {// prevent against empty selectors
        for (let i=0; i<host.classList.length; i++) {
            if (host.classList[i] === className) {
                elements = []
                elements.push(host)
            }
        }
    
        if (componentShadowRoot) {
            if (componentShadowRoot.querySelectorAll(`.${className}`)) {
                !elements ? elements = [] : null
    
                for (let i=0; i<componentShadowRoot.querySelectorAll(`.${className}`).length; i++) {
                    elements.push(componentShadowRoot.querySelectorAll(`.${className}`)[i])
                }
            }
        }
    
        elements.length === 0 ? elements = null : null
    }

    return elements
}


// returns the first element that matches the value of the ID
module.exports.getElementById = function getElementById (id) {
    let host = this.state.component,
    componentShadowRoot = host.shadowRoot,
    element = null

    if (id !== '') {// prevent against empty selectors
        if (host.id === id) {
            element = host
        }

        if (!element && componentShadowRoot) {
            element = componentShadowRoot.querySelector(`#${id}`)
        }
    }

    return element
}


// returns an array of elements matching the specified name attribute
module.exports.getElementsByName = function getElementsByName (name) {
    let host = this.state.component,
    componentShadowRoot = host.shadowRoot,
    elements = null

    if (name !== '') {// prevent against empty selectors
        if (host.name === name) {
            elements = []
            elements.push(host)
        }

        if (componentShadowRoot) {
            if (componentShadowRoot.querySelectorAll('*')) {
                !elements ? elements = [] : null
    
                for (let i=0; i<componentShadowRoot.querySelectorAll('*').length; i++) {
                    if (componentShadowRoot.querySelectorAll('*')[i].name === name) {
                        elements.push(componentShadowRoot.querySelectorAll('*')[i])
                    }
                }
            }
        }
        
        elements.length === 0 ? elements = null : null
    }

    return elements
}


// returns a non-live array of nodes
module.exports.getElementsByTagName = function getElementsByTagName (tagName) {
    let host = this.state.component,
    componentShadowRoot = host.shadowRoot,
    elements = null

    if (tagName !== '') {// prevent against empty selectors
        if (host.tagName === tagName.toUpperCase()) {
            elements = []
            elements.push(host)
        }
    
        if (componentShadowRoot) {
            if (componentShadowRoot.querySelectorAll(tagName)) {
                !elements ? elements = [] : null
    
                for (let i=0; i<componentShadowRoot.querySelectorAll(tagName).length; i++) {
                    elements.push(componentShadowRoot.querySelectorAll(tagName)[i])
                }
            }
        }
    
        elements.length === 0 ? elements = null : null
    }

    return elements
}


// returns the first element that matches the value of the selector
module.exports.querySelector = function querySelector (selector) {
    let host = this.state.component,
    componentShadowRoot = host.shadowRoot,
    element = null

    if (selector !== '') {// prevent against empty selectors
        if (host.host) {
            // is root element within a shadow root?
            if (host.host.querySelectorAll(selector)) {
                for (let i=0; i<host.host.querySelectorAll(selector).length; i++) {
                    if (host.host.querySelectorAll(selector)[i] === host) {
                        element = host.host.querySelectorAll(selector)[i]
                    }
                }
            }
        } else {
            // is root element within the dom?
            if (document.querySelectorAll(selector)) {
                for (let i=0; i<document.querySelectorAll(selector).length; i++) {
                    if (document.querySelectorAll(selector)[i] === host) {
                        element = document.querySelectorAll(selector)[i]
                    }
                }
            }
        }
    
        if (!element && componentShadowRoot) {
            element = componentShadowRoot.querySelector(selector)
        }    
    }

    return element
}


// returns a non-live array of nodes
module.exports.querySelectorAll = function querySelectorAll (selector) {
    let host = this.state.component,
    componentShadowRoot = host.shadowRoot,
    elements = null

    if (selector !== '') {// prevent against empty selectors
        if (host.host) {
            // is root element within a shadow root?
            if (host.host.querySelectorAll(selector)) {
                elements = []
    
                for (let i=0; i<host.host.querySelectorAll(selector).length; i++) {
                    if (host.host.querySelectorAll(selector)[i] === host) {
                        elements.push(host.host.querySelectorAll(selector)[i])
                    }
                }
            }
        } else {
            // is root element within the dom?
            if (document.querySelectorAll(selector)) {
                elements = []
    
                for (let i=0; i<document.querySelectorAll(selector).length; i++) {
                    if (document.querySelectorAll(selector)[i] === host) {
                        elements.push(document.querySelectorAll(selector)[i])
                    }
                }
            }
        }
    
        if (componentShadowRoot) {
            if (componentShadowRoot.querySelectorAll(selector)) {
                !elements ? elements = [] : null
    
                for (let i=0; i<componentShadowRoot.querySelectorAll(selector).length; i++) {
                    elements.push(componentShadowRoot.querySelectorAll(selector)[i])
                }
            }
        }

        elements.length === 0 ? elements = null : null
    }

    return elements
}


module.exports.getElementByRef = function getElementByRef (ref) {
    this[`${ref}`] = ref
    return this[`${ref}`].current
}


// this function gives access to the shadow DOM once the contents are fully loaded
// it accepts a callback function, and returns a promise creating 2 way access to shadow DOM elements
module.exports.ready = async function ready (callbackFN) {
    let host = this.state.component, componentShadowRoot, shadowDomLoaded
    await host

    if (!host) {
        this.ready(callbackFN)
    } else {
        componentShadowRoot = host.shadowRoot
        await componentShadowRoot
    
        shadowDomLoaded = componentShadowRoot.childNodes
        await shadowDomLoaded
    
        if (shadowDomLoaded && callbackFN) {
            callbackFN(this)
        }
    }

    return this
}
